<template>
  <div id="file">
    <Card>
      <p slot="title">
        文件列表
      </p>
      <div style="text-align:center;">
        <Table :columns="columns" :data="allData" stripe></Table>           
      </div>
    </Card>
  </div>
</template>

<script>
import filters from '@/filters/index'
export default {
    name: 'file',
    data () {
      return {
        columns:[{
          title: "编号",
          key: 'id'
        },{
          title: "作者",
          key: "username"
        },{
          title: "发表时间",
          key: "post_time",
          render: (h, params) => {                         
              return h('div',
                  filters.timeFormat(params.row.post_time) 
              )
          } 
        },{
          title: "文件描述",
          key: "description"
        },{
            title: '删除',
            key: 'action',
            width: 150,
            align: 'center',
            render: (h, params) => {
                return h('div', [                                
                    h('Button', {
                        props: {
                            type: 'error',
                            size: 'small'
                        },
                        on: {
                          click: () => {
                            this.delFile(params.index,params.row.id);
                          }  
                        } 
                    }, '删除')
                ]);
            }
        }],
        allData: []
      }
    },
    created () {
      this.getShare();
    },
    methods: {
      getShare () {
        this.$http.get('/file/shareFile')
          .then(res => {
            this.allData = res.data.msg;
          }).catch(err => {
            this.$Message.error("出错啦。。。")
          })
      },
      delFile (index,id) {
        this.$http.post('file/deleteFile',{
          'fileId': id
        }).then(res => {
          this.allData.splice(index,1);
          this.$Message.success("删除成功！");
        }).catch(err => {
          this.$Message.error("出错啦。。。");
        })
      }
    }
}
</script>
<style lang="scss" scoped>

</style>



